import {
  Directive,
  ElementRef,
  Input,
  OnDestroy,
  DoCheck,
  Renderer,
  Renderer2,
  KeyValueDiffers,
  SimpleChanges, OnChanges,
  SecurityContext
} from '@angular/core';
import {NgStyle} from '@angular/common';

import {BaseFxDirectiveAdapter} from './base-adapter';
import {BreakPointRegistry} from './../../media-query/breakpoints/break-point-registry';
import {MediaChange} from '../../media-query/media-change';
import {MediaMonitor} from '../../media-query/media-monitor';
import {extendObject} from '../../utils/object-extend';
import {DomSanitizer} from '@angular/platform-browser';

import {
  NgStyleRawList,
  NgStyleType,
  NgStyleSanitizer,
  ngStyleUtils as _
} from '../../utils/style-transforms';

/**
 * Directive to add responsive support for ngStyle.
 *
 */

export class StyleDirective extends NgStyle implements DoCheck, OnChanges, OnDestroy {

  /**
   * Intercept ngStyle assignments so we cache the default styles
   * which are merged with activated styles or used as fallbacks.
   */
  
  set styleBase(val: NgStyleType) {
    this._base.cacheInput('style', val, true);
    this.ngStyle = this._base.inputMap['style'];
  }

  /* tslint:disable */
      set ngStyleXs(val: NgStyleType) { this._base.cacheInput('styleXs', val, true); }
      set ngStyleSm(val: NgStyleType) {  this._base.cacheInput('styleSm', val, true); };
      set ngStyleMd(val: NgStyleType) { this._base.cacheInput('styleMd', val, true); };
      set ngStyleLg(val: NgStyleType) { this._base.cacheInput('styleLg', val, true);};
      set ngStyleXl(val: NgStyleType) { this._base.cacheInput('styleXl', val, true); };

   set ngStyleLtSm(val: NgStyleType) { this._base.cacheInput('styleLtSm', val, true); };
   set ngStyleLtMd(val: NgStyleType) { this._base.cacheInput('styleLtMd', val, true); };
   set ngStyleLtLg(val: NgStyleType) { this._base.cacheInput('styleLtLg', val, true); };
   set ngStyleLtXl(val: NgStyleType) { this._base.cacheInput('styleLtXl', val, true); };

   set ngStyleGtXs(val: NgStyleType) { this._base.cacheInput('styleGtXs', val, true); };
   set ngStyleGtSm(val: NgStyleType) { this._base.cacheInput('styleGtSm', val, true);} ;
   set ngStyleGtMd(val: NgStyleType) { this._base.cacheInput('styleGtMd', val, true);};
   set ngStyleGtLg(val: NgStyleType) { this._base.cacheInput('styleGtLg', val, true); };

  /** Deprecated selectors */
        set styleXs(val: NgStyleType) { this._base.cacheInput('styleXs', val, true); }
        set styleSm(val: NgStyleType) { this._base.cacheInput('styleSm', val, true); };
        set styleMd(val: NgStyleType) { this._base.cacheInput('styleMd', val, true);};
        set styleLg(val: NgStyleType) { this._base.cacheInput('styleLg', val, true); };
        set styleXl(val: NgStyleType) { this._base.cacheInput('styleXl', val, true); };

     set styleLtSm(val: NgStyleType) { this._base.cacheInput('styleLtSm', val, true); };
     set styleLtMd(val: NgStyleType) { this._base.cacheInput('styleLtMd', val, true); };
     set styleLtLg(val: NgStyleType) { this._base.cacheInput('styleLtLg', val, true);};
     set styleLtXl(val: NgStyleType) { this._base.cacheInput('styleLtXl', val, true); };

     set styleGtXs(val: NgStyleType) { this._base.cacheInput('styleGtXs', val, true); };
     set styleGtSm(val: NgStyleType) { this._base.cacheInput('styleGtSm', val, true); };
     set styleGtMd(val: NgStyleType) { this._base.cacheInput('styleGtMd', val, true);};
     set styleGtLg(val: NgStyleType) { this._base.cacheInput('styleGtLg', val, true); };

  /* tslint:enable */
  /**
   *  Constructor for the ngStyle subclass; which adds selectors and
   *  a MediaQuery Activation Adapter
   */
  constructor(private monitor: MediaMonitor,
              protected _bpRegistry: BreakPointRegistry,
              protected _sanitizer: DomSanitizer,
              _differs: KeyValueDiffers,
              _ngEl: ElementRef,
              _oldRenderer: Renderer,
              _renderer: Renderer2) {

    // TODO: this should use Renderer2 when the NgStyle signature is switched over to it.
    super(_differs, _ngEl, _oldRenderer);

    // Build adapter, `cacheInput()` interceptor, and get current inline style if any
    this._buildAdapter(monitor, _ngEl, _renderer);
    this._base.cacheInput('style', _ngEl.nativeElement.getAttribute("style"), true);
  }

  // ******************************************************************
  // Lifecycle Hookks
  // ******************************************************************

  /**
   * For @Input changes on the current mq activation property
   */
  ngOnChanges(changes: SimpleChanges) {
    if (this._base.activeKey in changes) {
      this._updateStyle();
    }
  }

  /**
   * For ChangeDetectionStrategy.onPush and ngOnChanges() updates
   */
  ngDoCheck() {
    if (!this._base.hasMediaQueryListener) {
      this._configureMQListener();
    }
    super.ngDoCheck();
  }

  ngOnDestroy() {
    this._base.ngOnDestroy();
  }

  // ******************************************************************
  // Internal Methods
  // ******************************************************************

  /**
     * Build an mqActivation object that bridges
     * mql change events to onMediaQueryChange handlers
     */
    protected _configureMQListener() {
      this._base.listenForMediaQueryChanges('style', '', (changes: MediaChange) => {
        this._updateStyle(changes.value);

        // trigger NgClass::_applyIterableChanges()
        super.ngDoCheck();
      });
    }

  // ************************************************************************
  // Private Internal Methods
  // ************************************************************************

  /**
   * Use the currently activated input property and assign to
   * `ngStyle` which does the style injections...
   */
  protected _updateStyle(value?: NgStyleType) {
    let style = value || this._base.queryInput("style") || '';
    if (this._base.mqActivation) {
      style = this._base.mqActivation.activatedInput;
    }

    // Delegate subsequent activity to the NgStyle logic
    this.ngStyle = style;
  }


  /**
   * Build MediaQuery Activation Adapter
   * This adapter manages listening to mediaQuery change events and identifying
   * which property value should be used for the style update
   */
  protected _buildAdapter(monitor: MediaMonitor, _ngEl: ElementRef, _renderer: Renderer2) {
    this._base = new BaseFxDirectiveAdapter('style', monitor, _ngEl, _renderer);
    this._buildCacheInterceptor();
  }


  /**
   * Build intercept to convert raw strings to ngStyleMap
   */
  protected _buildCacheInterceptor() {
    let cacheInput = this._base.cacheInput.bind(this._base);
    this._base.cacheInput = (key?: string, source?: any, cacheRaw = false, merge = true) => {
      let styles = this._buildStyleMap(source);
      if (merge) {
        styles = extendObject({}, this._base.inputMap['style'], styles);
      }
      cacheInput(key, styles, cacheRaw);
    };
  }
  /**
   * Convert raw strings to ngStyleMap; which is required by ngStyle
   * NOTE: Raw string key-value pairs MUST be delimited by `;`
   *       Comma-delimiters are not supported due to complexities of
   *       possible style values such as `rgba(x,x,x,x)` and others
   */
  protected _buildStyleMap(styles: NgStyleType) {
    let sanitizer: NgStyleSanitizer = (val: any) => {
      // Always safe-guard (aka sanitize) style property values
      return this._sanitizer.sanitize(SecurityContext.STYLE, val);
    };
    if (styles) {
      switch ( _.getType(styles) ) {
        case 'string':  return _.buildMapFromList(_.buildRawList(styles), sanitizer);
        case 'array' :  return _.buildMapFromList(styles as NgStyleRawList, sanitizer);
        case 'set'   :  return _.buildMapFromSet(styles, sanitizer);
        default      :  return _.buildMapFromSet(styles, sanitizer);
      }
    }
    return styles;
  }

  /**
   * Special adapter to cross-cut responsive behaviors
   * into the StyleDirective
   */
  protected _base: BaseFxDirectiveAdapter;

static decorators: DecoratorInvocation[] = [
{ type: Directive, args: [{
  selector: `
    [style.xs], [style.sm], [style.md], [style.lg], [style.xl],
    [style.lt-sm], [style.lt-md], [style.lt-lg], [style.lt-xl],
    [style.gt-xs], [style.gt-sm], [style.gt-md], [style.gt-lg],
    [ngStyle],
    [ngStyle.xs], [ngStyle.sm], [ngStyle.lg], [ngStyle.xl],
    [ngStyle.lt-sm], [ngStyle.lt-md], [ngStyle.lt-lg], [ngStyle.lt-xl],
    [ngStyle.gt-xs], [ngStyle.gt-sm], [ngStyle.gt-md], [ngStyle.gt-lg]
  `
}, ] },
];
/** @nocollapse */
static ctorParameters: () => ({type: any, decorators?: DecoratorInvocation[]}|null)[] = () => [
{type: MediaMonitor, },
{type: BreakPointRegistry, },
{type: DomSanitizer, },
{type: KeyValueDiffers, },
{type: ElementRef, },
{type: Renderer, },
{type: Renderer2, },
];
static propDecorators: {[key: string]: DecoratorInvocation[]} = {
'styleBase': [{ type: Input, args: ['ngStyle', ] },],
'ngStyleXs': [{ type: Input, args: ['ngStyle.xs', ] },],
'ngStyleSm': [{ type: Input, args: ['ngStyle.sm', ] },],
'ngStyleMd': [{ type: Input, args: ['ngStyle.md', ] },],
'ngStyleLg': [{ type: Input, args: ['ngStyle.lg', ] },],
'ngStyleXl': [{ type: Input, args: ['ngStyle.xl', ] },],
'ngStyleLtSm': [{ type: Input, args: ['ngStyle.lt-sm', ] },],
'ngStyleLtMd': [{ type: Input, args: ['ngStyle.lt-md', ] },],
'ngStyleLtLg': [{ type: Input, args: ['ngStyle.lt-lg', ] },],
'ngStyleLtXl': [{ type: Input, args: ['ngStyle.lt-xl', ] },],
'ngStyleGtXs': [{ type: Input, args: ['ngStyle.gt-xs', ] },],
'ngStyleGtSm': [{ type: Input, args: ['ngStyle.gt-sm', ] },],
'ngStyleGtMd': [{ type: Input, args: ['ngStyle.gt-md', ] },],
'ngStyleGtLg': [{ type: Input, args: ['ngStyle.gt-lg', ] },],
'styleXs': [{ type: Input, args: ['style.xs', ] },],
'styleSm': [{ type: Input, args: ['style.sm', ] },],
'styleMd': [{ type: Input, args: ['style.md', ] },],
'styleLg': [{ type: Input, args: ['style.lg', ] },],
'styleXl': [{ type: Input, args: ['style.xl', ] },],
'styleLtSm': [{ type: Input, args: ['style.lt-sm', ] },],
'styleLtMd': [{ type: Input, args: ['style.lt-md', ] },],
'styleLtLg': [{ type: Input, args: ['style.lt-lg', ] },],
'styleLtXl': [{ type: Input, args: ['style.lt-xl', ] },],
'styleGtXs': [{ type: Input, args: ['style.gt-xs', ] },],
'styleGtSm': [{ type: Input, args: ['style.gt-sm', ] },],
'styleGtMd': [{ type: Input, args: ['style.gt-md', ] },],
'styleGtLg': [{ type: Input, args: ['style.gt-lg', ] },],
};
}

interface DecoratorInvocation {
  type: Function;
  args?: any[];
}
